<template  class="Nhập hàng bình thường>Nhập thủ công">
    <h3 class="title">{{ $t('Nhập hàng thủ công') }}:</h3>
    <el-divider />

    <el-row :gutter="24" justify="center">
        <el-col :sm="20">
            <el-row :gutter="24">
                <el-col :md="8">
                    <el-form-item :label="$t('Khu xưởng/Phòng y tế')">
                        <el-input v-model="formMain.factoryZonee" readonly />
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="24">
                <el-col :md="8">
                    <el-form-item :label="$t('Số hiệu hàng nhập')" :rules="[{ required: true },]">
                        <el-input @change="GetMedicineInfor()" v-model="formMain.medicineID"
                            :placeholder="$t('Nhập vào số hiệu hàng nhập')" clearable />
                    </el-form-item>
                </el-col>
                <el-col :md="8">
                    <el-form-item :label="$t('Tên hàng nhập')">
                        <el-input v-model="formMain.medicineName" readonly />
                    </el-form-item>
                </el-col>
                <el-col :md="8">
                    <el-form-item :label="$t('Ngày nhập hệ thống')">
                        <el-input v-model="formMain.systemInputDate" readonly />
                    </el-form-item>
                </el-col>
            </el-row>

            <el-row :gutter="24">
                <el-col :md="8">
                    <el-form-item :label="$t('Số lượng nhập')" :rules="[{ required: true },]">
                        <el-input-number @change="GetTotalInOutInforOfAMedicine()" v-model="formMain.inQty"
                            :placeholder="$t('Nhập vào số lượng nhập')" clearable :min="0" :max="1000000000" :precision="0"
                            value-on-clear="min" />
                    </el-form-item>
                </el-col>
                <el-col :md="8">
                    <el-form-item :label="$t('Số lượng tồn')">
                        <el-input v-model="formMain.totalQty" readonly />
                    </el-form-item>
                    <label style="font-size: small; color: red;">
                        {{ $t('Số lượng tồn = Số lượng tồn cũ + Số lượng nhập mới') }} </label>
                </el-col>
                <el-col :md="8">
                    <el-form-item :label="$t('Ngày nhập thực tế')" :rules="[{ required: true },]">
                        <el-date-picker v-model="formMain.realInputDate" clearable type="date" format="YYYY/MM/DD"
                            value-format="YYYY/MM/DD" :placeholder="$t('Chọn ngày nhập thực tế')" />
                    </el-form-item>
                </el-col>
            </el-row>
            <br />
            <el-row :gutter="24">
                <el-col :md="8">
                    <el-form-item :label="$t('Hạn sử dụng')" :rules="[{ required: true },]">
                        <el-date-picker @blur="OnchangeExpiredDate()" v-model="formMain.medicineExpiredDate" clearable
                            type="date" format="YYYY/MM/DD" value-format="YYYY/MM/DD"
                            :placeholder="$t('Chọn hạn sử dụng')" />
                    </el-form-item>
                </el-col>
                <el-col :md="8">
                    <el-form-item :label="$t('Đầu đơn mua')">
                        <el-input v-model="formMain.buyDocNum" clearable :placeholder="$t('Nhập vào đầu đơn mua')" />
                    </el-form-item>
                </el-col>
                <el-col :md="8">
                    <el-form-item :label="$t('Quy cách')">
                        <el-input v-model="formMain.medicineObjUnit" readonly />
                    </el-form-item>
                </el-col>
            </el-row>

            <el-row class="row-bg" justify="center">
                <el-col :span="2">
                    <el-form-item>
                        <el-popconfirm :confirm-button-text="$t('Có')" :cancel-button-text="$t('Không')" :icon="InfoFilled"
                            icon-color="#626AEF" :title="$t('Bạn có chắc chắn muốn thao tác?')" @confirm="ImportToWHByNormalType()"
                            @cancel="cancelEvent"> <template #reference>
                                
                                    <el-button type="primary" size="large" plain>
                                        <el-icon>
                                            <Position />
                                        </el-icon>
                                        &nbsp;
                                        {{ $t('Nhập kho') }}
                                    </el-button>
                            </template>
                        </el-popconfirm>


                    </el-form-item>
                </el-col>
            </el-row>
        </el-col>
    </el-row>
</template>
<script >
import api from "@/model/api.js"
import { ElMessage } from 'element-plus'
export default {
    name: "ImportedGoodsManual",
    data() {
        return {
            formMain: {
                medicineID: "",
                medicineName: "",
                medicineNameVi: "",
                medicineNameEn: "",
                systemInputDate: "",
                realInputDate: "",
                inQty: 0,
                totalQty: "",
                medicineExpiredDate: "",
                buyDocNum: "",
                medicineObjUnit: "", // quy cach
                medicineObjUnitVi: "", // quy cach
                medicineObjUnitEn: "", // quy cach
                factoryZonee: "",
                inputType: "",
            }
        }
    },
    props: {
        factoryZone: String
    },
    methods: {
        inInt() {
            this.formMain.factoryZonee = this.factoryZone;
        },
        ResetView() {
            this.formMain.medicineNameVi = "";
            this.formMain.medicineNameEn = "";
            this.formMain.medicineName = "";
            this.formMain.medicineObjUnitVi = "";
            this.formMain.medicineObjUnitEn = "";
            this.formMain.medicineObjUnit = "";
            this.formMain.systemInputDate = "";
            this.formMain.totalQty = "";
            this.formMain.realInputDate = "";
            this.formMain.medicineExpiredDate = "";
            this.formMain.buyDocNum = "";
            this.formMain.inQty = 0;


        },
        ImportToWHByNormalType() {

            api.ImportGoodsToWareHouseByNormalType(
                this.formMain,
                (dat) => {
                    if (dat?.data?.status == "success") {
                        ElMessage({
                            message: dat.data.message,
                            type: 'success',
                        })
                        this.formMain.medicineID = ""
                        this.ResetView()
                    }
                    else  //error
                    {

                        ElMessage({
                            message: dat.data.message,
                            type: 'error',
                            dangerouslyUseHTMLString: true,
                            duration: 5000
                        })
                    }
                },
                (er) => {
                    ElMessage.error(er.message)
                })



        },
        OnchangeExpiredDate() {
            this.formMain.totalQty = "";

        },
        GetMedicineInfor() {
            this.ResetView();
            if (this.formMain.medicineID) {
                api.GetMedicineInfor({ medicineID: this.formMain.medicineID },
                    (dat) => {

                        if (dat?.data?.data) {
                            let tempDat = dat?.data?.data;
                            this.formMain.medicineNameVi = tempDat.F_MEDICINE_NAME_VI
                            this.formMain.medicineNameEn = tempDat.F_MEDICINE_NAME_EN
                            this.formMain.medicineName = this.formMain.medicineNameVi + "/" + this.formMain.medicineNameEn

                            this.formMain.medicineObjUnitVi = tempDat.F_MEDICINE_OBJ_UNIT_VI
                            this.formMain.medicineObjUnitEn = tempDat.F_MEDICINE_OBJ_UNIT_EN
                            this.formMain.medicineObjUnit = tempDat.F_MEDICINE_OBJ_UNIT_VI + "/" + tempDat.F_MEDICINE_OBJ_UNIT_EN
                            this.formMain.systemInputDate = tempDat.F_SYSDATE1

                        }
                    },
                    (er) => {
                        ElMessage.error(er.message)
                    })
            }

        },
        GetTotalInOutInforOfAMedicine() {
            this.formMain.totalQty = "";
            if (!this.formMain.medicineExpiredDate) {
                ElMessage.error(this.$t('Vui lòng chọn hạn sử dụng trước'))
                return
            }

            if (
                isNaN(this.formMain.inQty) == false &&
                parseInt(this.formMain.inQty) != 0 &&
                this.formMain.medicineID &&
                this.formMain.medicineExpiredDate &&
                this.formMain.factoryZonee) {
                api.GetTotalInOutInforOfAMedicine(
                    {
                        medicineId: this.formMain.medicineID,
                        medicineExpiredDate: this.formMain.medicineExpiredDate,
                        factoryZone: this.formMain.factoryZonee
                    }
                    ,
                    (dat) => {


                        if (dat?.data?.status == "success") {

                            if (dat?.data?.data) {
                                if (isNaN(dat?.data?.data?.F_TOTAL_QTY) == false
                                    && isNaN(this.formMain.inQty) == false
                                )  // is number
                                {
                                    this.formMain.totalQty
                                        = (parseInt(this.formMain.inQty) + parseInt(dat?.data?.data?.F_TOTAL_QTY)).toString()

                                }
                            }
                            else {
                                this.formMain.totalQty = (parseInt(this.formMain.inQty)).toString()
                            }
                        }
                        else //error
                        {
                            ElMessage.error(dat?.data?.message)
                            return
                        }


                    },
                    (er) => {
                        ElMessage.error(er.message)
                    })
            }


        }

    },
    mounted() {
        this.inInt();
    }
}
</script>
<style scoped>
.title {
    text-align: left;
    padding-left: 5px;
}

.tb-main td {
    border-collapse: collapse;
    padding: 5px 5px;
    padding-right: 5px;
    padding-left: 5px;
    padding-top: 10px;
    border: 0.1px solid #00000000;
}
</style>