<template class="Nhập hàng bình thường>Nhập hàng loạt">
    <h3 class="title">{{ $t('Nhập hàng hàng loạt') }}:</h3>
    <el-divider />
    <el-row :gutter="24" justify="center">
        <el-col :sm="20">
            <el-row :gutter="24">
                <el-col :md="8">
                    <el-form-item :label="$t('Khu xưởng/Phòng y tế')">
                        <el-input v-model="formMain.factoryZonee" readonly />
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="24">
                <el-col :lg="10">
                    <el-upload ref="uploadRef" class="upload-demo" :limit="1" 
                     :on-change="handleChangeFile"
                     :on-remove="handleRemoveFile"
                        :auto-upload="false">
                        <template #trigger>
                            <el-button type="primary" size="large" plain style="margin-bottom: 10px;">
                                <el-icon>
                                    <Folder />
                                </el-icon>
                                &nbsp;
                                {{ $t('Chọn file') }}
                            </el-button>
                        </template>
                        &nbsp;

                        <el-popconfirm :confirm-button-text="$t('Có')" :cancel-button-text="$t('Không')" :icon="InfoFilled"
                            icon-color="#626AEF" :title="$t('Bạn có chắc chắn muốn thao tác?')" @confirm="ImportToWHByNormalType()"
                            @cancel="cancelEvent"> <template #reference>
                                    <el-button style="margin-top: -6px;" type="warning" size="large" plain
                                    >
                                        <el-icon>
                                            <Upload />
                                        </el-icon>
                                        &nbsp;
                                        {{ $t('Tải lên danh sách') }}
                                    </el-button>
                            </template>
                        </el-popconfirm>



                      
                        <template #tip>
                            <div class="el-upload__tip" style="color: red;">
                                {{ $t('File excel có định dạng .xls/xlsx nhỏ hơn 10mb') }}
                            </div>
                        </template>
                    </el-upload>


                </el-col>
                <br>
                <el-col :lg="4">
                    <el-button type="success" size="large" style="text-decoration: none" :href="getSampleFile" tag="a"
                        rel="noopener noreferrer" plain>
                        <el-icon>
                            <Download />
                        </el-icon>
                        &nbsp;
                        {{ $t('Tải xuống form mẫu') }}
                    </el-button>
                </el-col>
            </el-row>
            <br />
        </el-col>
    </el-row>
</template>
<script>
import { ElMessageBox } from 'element-plus'
import { ElMessage } from 'element-plus'
import api from "@/model/api.js"
export default {
    name: "ImportedGoodsMore",
    data() {
        return {
            formMain: {
                factoryZonee: "",
                fileUpload: null,
            }
        }
    },
    props: {
        factoryZone: String
    },
    methods:
    {
        Test() {
            console.log(this.$refs.uploadRef)

        },
        handleRemoveFile(file)
        {
            console.log(file)
            this.formMain.fileUpload =null
        },
        handleChangeFile(file) {
            this.formMain.fileUpload = file
        },
        ImportToWHByNormalType() {
            try {

                if (!this.formMain.fileUpload?.raw) {
                    ElMessage.error(this.$t('Vui lòng chọn file trước tiên'))
                    return
                }
                else 
                {
                    if (
                        this.formMain.fileUpload.raw.type == "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet"
                        || this.formMain.fileUpload.raw.type == "application/vnd.ms-excel"
                    ) {
                        console.log("")
                    }
                    else {
                        ElMessage.error(this.$t('Vui lòng chọn file excel với định dạng xls/xlsx'))
                        return
                    }

                    if (this.formMain.fileUpload.raw.size > 10485760)  //10mb
                    {
                        ElMessage.error(this.$t('Kích thước file phải nhỏ hơn 10 mb'))
                        return
                    }
                    var form = new FormData()
                    form.append("factoryZonee", this.formMain.factoryZonee)
                    form.append("file", this.formMain.fileUpload.raw)
                    api.ImportNormalType(form,
                        (dat) => {
                            if (dat?.data) {
                                if (dat?.data?.status == "success") {

                                    ElMessageBox.alert(
                                        `<h3 style="color:green"> ${this.$t('Danh sách tải lên thành công:')} </h3>
                                     <div style="background-color:green; color:white"> ${dat?.data?.data.successLs} </div>
                                     <h3 style="color:red"> ${this.$t('Danh sách tải lên thất bại:')} </h3>
                                     <div style="background-color:red; color:white"> ${dat?.data?.data.errorLs} </div>
                                    `,
                                        this.$t('Thông báo'),
                                        {
                                            dangerouslyUseHTMLString: true,
                                            showClose: false
                                        }
                                    )
                                    this.formMain.fileUpload = null;

                                }
                                else {  //error
                                    ElMessage.error(dat?.data?.message)
                                }
                            }

                        },
                        (er) => {
                            ElMessage.error(er.message)
                        })
                }
            }
            catch(er)
            {
              console.log(er)
            }
            finally {
                this.$refs.uploadRef?.clearFiles()
            }

        }
    },
    computed: {
        getSampleFile() {
            return api.DownloadImportNormalSampleFile();
        }
    },
    mounted() {
        this.formMain.factoryZonee = this.factoryZone
    }
}
</script>
<style scoped>
.title {
    text-align: left;
    padding-left: 5px;
}
</style>