<template class="Form chính nhập hàng bình thường">
    <el-form label-width="auto" style="max-width: 500px" v-if="isShowSelectFactoryZone">
        <el-form-item :label="$t('Khu xưởng/Phòng y tế')">
            <el-select v-model="currentFactoryZone" :placeholder="$t('Vui lòng chọn khu xưởng/Phòng y tế')" clearable>
                <el-option v-for="item in FactoryZoneLs" :key="item.value" :label="item.label" :value="item.value" />
            </el-select>
        </el-form-item>
        <el-form-item>
            <div>
                <el-button size="large" type="success" plain @click="ImportedGoodsManual()">
                    <el-icon>
                        <ArrowRightBold />
                    </el-icon>
                    &nbsp;{{ $t('Nhập hàng thủ công') }}</el-button>
                &nbsp;
                <el-button size="large" type="primary" plain @click="ImportedGoodsMore()">
                    <el-icon>
                        <DArrowRight />
                    </el-icon>
                    &nbsp;{{ $t('Nhập hàng hàng loạt') }}</el-button>

            </div>
        </el-form-item>
    </el-form>
    <div class="imported-good-frm" v-else>
        <div style="text-align:left">
            <el-button style="border:none" @click="ResetSome()">
                <el-icon>
                    <Back />
                </el-icon>
                &nbsp; {{ $t('Trở lại') }}
            </el-button>
        </div>
        <ImportedGoodsManual v-if="isShowImportedGoodsManual" :factoryZone="currentFactoryZone" />
        <ImportedGoodsMore v-else :factoryZone="currentFactoryZone" />
    </div>
</template>
<script>
import { ElMessage } from 'element-plus'
import ImportedGoodsManual from "@/components/ManageImportedGoods/ImportedGoodsManual.vue"
import ImportedGoodsMore from "@/components/ManageImportedGoods/ImportedGoodsMore.vue"
import api from "@/model/api.js"
export default
    {
        name: 'ImportedGoods',
        components: { ImportedGoodsManual, ImportedGoodsMore },
        data() {
            return {
                currentFactoryZone: "",
                FactoryZoneLs: [],
                isShowImportedGoodsManual: false,
                isShowSelectFactoryZone: true,
            }
        },
        methods: {
            GetFactoryZone() {
                this.FactoryZoneLs = [];
                api.GetFactoryZone((dat) => {
                    if (dat.data.data) {
                        dat.data.data.forEach((item) => {
                            this.FactoryZoneLs.push({ label: item.F_DATA2, value: item.F_DATA2 })
                        })

                    }

                }, (er) => {
                    ElMessage.error(er.message)
                });
            },
            ImportedGoodsManual() {
                if(!this.currentFactoryZone)
                {
                    ElMessage.error(this.$t("Vui lòng chọn nhà xưởng đầu tiên"))
                    return;
                }
                this.isShowSelectFactoryZone = false;
                this.isShowImportedGoodsManual = true;
            },
            ImportedGoodsMore()
            {
                if(!this.currentFactoryZone)
                {
                    ElMessage.error(this.$t("Vui lòng chọn nhà xưởng đầu tiên"))
                    return;
                }
                this.isShowSelectFactoryZone = false;
                this.isShowImportedGoodsManual = false;
            }
            , ResetSome() {
                this.isShowImportedGoodsManual = false;
                this.isShowSelectFactoryZone = true;
                this.currentFactoryZone = "";
            }
        },
        mounted() {
            this.GetFactoryZone();
        }
    }

</script>
<style scoped></style>